import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarCheck, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import './Hero.css';
import { isMobile } from 'react-device-detect';

function Hero() {
	const navigate = useNavigate();
	const [goUp, setGoUp] = useState(false);

	const scrollToTop = () => {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	};

	const handleBookAppointmentClick = () => {
		navigate('/appointment');
	};

	useEffect(() => {
		const onPageScroll = () => {
			if (window.scrollY > 600) {
				setGoUp(true);
			} else {
				setGoUp(false);
			}
		};
		window.addEventListener('scroll', onPageScroll);

		return () => {
			window.removeEventListener('scroll', onPageScroll);
		};
	}, []);

	return (
		<div className="section-container" style={{ height: isMobile ? '80vh' : '' }}>
			<div className="hero-section">
				<div style={{ display: isMobile ? 'none' : '', padding: '150px 50px 0px 130px' }}>
					<div className="ad-section-container" />
				</div>
				<div className="text-section" style={{ padding: '150px 50px 0px 0' }}>
					<p className="text-headline">{`Work with us today`}</p>
					<h2 className="text-title">{`Impact through simple solutions`}</h2>
					<p className="text-descritpion">{`Let's talk. We listen and understand.
					No application is too complex as long as requirements are clear and collaborations are plenty.`}
					</p>
				</div>
			</div>
			<div className="hero-section" style={{ display: isMobile ? 'none' : '' }}>
				<div className="text-section" style={{ padding: '50px 50px 0 50px' }}>
					<h2 className="text-title">{`Committed to produce exceptional work`}</h2>
					<p className="text-descritpion">{`We are committed to producing exceptional software for our partners and work closely with our users throughout development to ensure that we are aligned with the end-goal.`}</p>
				</div>
				<div className="text-section" style={{ padding: '0px 50px 0px 0' }}>

					<button className="text-appointment-btn" type="button" onClick={handleBookAppointmentClick} style={{ margin: '80px' }}>
						<FontAwesomeIcon icon={faCalendarCheck} /> {`Contact Us To Find How`}
					</button>

				</div>
			</div>

			<div onClick={scrollToTop} className={`scroll-up ${goUp ? 'show-scroll' : ''}`}>
				<FontAwesomeIcon icon={faAngleUp} />
			</div>
		</div>
	);
}
export default Hero;